"use client";
import Link from "next/link";
import { useParams } from "next/navigation";
import { useQuery } from "@tanstack/react-query";
import { getCatalog } from "@/api/catalog";
import { getProductSingle } from "@/api/product";
import { Container } from "@/components/container";
import { CartIcon, CopyIcon, CrumbChevronDownIcon, HomeIcon } from "@/assets/icons";
import { CatalogData, CategoryData } from "@/types";
import { findCatalogItem } from "@/utils/catalog-slug"; // Faqat top-level katalogni topish uchun
import ThumbsGallery from "@/components/thumbs-gallery/ThumbsGallery";
import Image from "next/image";
import { BadgeCheck, ChevronRight, ChevronRightIcon, CircleAlert, Download, Share2, Truck } from "lucide-react";
import CommentsIcon from "@/assets/icons/CommentsIcon";
import QuestionCommentIcon from "@/assets/icons/QuestionCommentIcon";
import { copyToClipboard } from "@/utils";
import { AddToCompare, AddToFavorites } from "@/components/add-storage";
import FreePickUpIcon from "@/assets/icons/FreePickUpIcon";
import InStockIcon from "@/assets/icons/InStockIcon";
import StarIcon from "@/assets/icons/StarIcon";
import PriceFormatter from "@/components/format-price/PriceFormatter";
import { ProductDescription } from "@/components/product-description/ProductDescription";
import { useEffect, useRef, useState } from "react";
import { createPortal } from "react-dom";

// Breadcrumb (CategoryCrumb) komponenti – bu yerda agar item ichida subcatalogs yoki categories bo'lsa, dropdown menyu hosil qilinadi
export const CategoryCrumb = ({
  item,
  isLast,
}: {
  item: { name: string; href?: string; catalogItem?: CatalogData };
  isLast: boolean;
}) => {
  const [isHovered, setIsHovered] = useState(false);
  const [dropdownStyle, setDropdownStyle] = useState({ top: 0, left: 0 });
  const containerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (isHovered && containerRef.current) {
      const rect = containerRef.current.getBoundingClientRect();
      setDropdownStyle({ top: rect.bottom, left: rect.left });
    }
  }, [isHovered]);

  const childSubcatalogs = item.catalogItem?.subcatalogs || [];
  const childCategories = item.catalogItem?.categories || [];
  // Agar bu oxirgi element bo'lsa, dropdown chiqmasin
  const showDropdown =
    !isLast &&
    item.href !== undefined &&
    item.catalogItem &&
    (childSubcatalogs.length > 0 || childCategories.length > 0);

  return (
    <div
      ref={containerRef}
      className="flex items-center gap-[15px] relative"
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      <div className="flex items-center">
        {showDropdown && <CrumbChevronDownIcon className="mr-2" />}
        {item.href ? (
          <Link
            href={item.href}
            className={`font-normal text-xs ${
              !isLast ? "text-weekColor hover:underline" : "text-celBlue"
            }`}
          >
            {item.name}
          </Link>
        ) : (
          <span className="text-celBlue font-normal text-xs cursor-text">
            {item.name}
          </span>
        )}
      </div>

      {!isLast && <ChevronRight className="text-weekColor" size={14} />}

      {showDropdown &&
        isHovered &&
        createPortal(
          <div
            className="bg-white border shadow-md p-2 w-fit"
            style={{
              position: "absolute",
              top: dropdownStyle.top,
              left: dropdownStyle.left,
              zIndex: 10,
            }}
          >
            {childSubcatalogs.length > 0 && (
              <ul>
                {childSubcatalogs.map((sub) => (
                  <li key={sub.id}>
                    <Link
                      href={`/catalog/${sub.slug}`}
                      className="text-xs text-weekColor hover:underline flex"
                    >
                      <ChevronRightIcon className="text-weekColor" size={14} />
                      {sub.title}
                    </Link>
                  </li>
                ))}
              </ul>
            )}
            {childCategories.length > 0 && (
              <ul>
                {childCategories.map((cat) => (
                  <li key={cat.id}>
                    <Link
                      // Agar bu element katalog bosqichidagi child bo'lsa, URL quyidagicha bo'ladi:
                      // Masalan: /catalog/{catalog.slug}/categories/{cat.slug}
                      // Agar subcatalog uchun bo'lsa, quyidagi URL shaklida:
                      // /catalog/{catalog.slug}/{subcatalog.slug}/categories/{cat.slug}
                      href={
                        item.catalogItem && item.catalogItem.slug.includes("-sub")
                          ? `/catalog/${item.catalogItem.slug}/categories/${cat.slug}`
                          : `/catalog/${cat.slug}`
                      }
                      className="text-xs text-weekColor hover:underline flex"
                    >
                      <ChevronRightIcon className="text-weekColor" size={14} />
                      {cat.title}
                    </Link>
                  </li>
                ))}
              </ul>
            )}
          </div>,
          document.body
        )}
    </div>
  );
};

export default function ProductPage() {
  const { slug, subSlug, productSlug } = useParams() as {
    slug?: string;
    subSlug?: string;
    productSlug: string;
  };

  // Katalog ma'lumotlarini olish
  const { data: catalogData = [] } = useQuery({
    queryKey: ["catalog"],
    queryFn: getCatalog,
  });
  // Mahsulot ma'lumotlarini olish
  const { data: product = {} } = useQuery({
    queryKey: ["product_single"],
    queryFn: () => getProductSingle(productSlug),
  });

  // Top-level katalogni topish
  const catalogItem = slug ? findCatalogItem(catalogData, slug) : undefined;
  // Agar subSlug berilgan bo'lsa, katalog ichidagi subcataloglar orasidan mosini tanlaymiz
  const subcatalogItem =
    catalogItem && subSlug
      ? (catalogItem.subcatalogs || []).find(
          (sub: any) => sub.slug === subSlug
        )
      : undefined;
  // Oxirgi bosqich: mahsulot ma'lumotidan category obyekti (agar mavjud)
  // Bu yerda product.category obyektida category title va slug bo'lishi kutilmoqda
  const categoryItem = product.category;

  // Breadcrumblarni tuzish:
  // 1. "Каталог" – asosiy sahifa
  // 2. Katalog sarlavhasi
  // 3. Agar mavjud bo'lsa, subcatalog sarlavhasi (va uning child elementlari dropdownda)
  // 4. Va nihoyat, category sarlavhasi, ustiga bosilganda URL quyidagicha: 
  //    `/catalog/{catalog.slug}/{subcatalog.slug}/categories/{category.slug}`
  const breadcrumbPaths = [
    { name: "Каталог", href: "/catalog", catalogItem: undefined },
    ...(catalogItem
      ? [
          {
            name: catalogItem.title,
            href: `/catalog/${catalogItem.slug}`,
            catalogItem: catalogItem,
          },
        ]
      : []),
    ...(subcatalogItem
      ? [
          {
            name: subcatalogItem.title,
            href: `/catalog/${catalogItem?.slug}/${subcatalogItem.slug}`,
            catalogItem: subcatalogItem,
          },
        ]
      : []),
    ...(categoryItem
      ? [
          {
            name: categoryItem.title,
            href: `/catalog/${catalogItem?.slug}/${subcatalogItem?.slug}/categories/${categoryItem.slug}`,
            catalogItem: undefined, // Oxirgi bosqich dropdown talab qilinmaydi
          },
        ]
      : []),
  ];

  return (
    <Container className="pb-[58px]">
      <div className="flex items-center pl-2 sm:pl-1 gap-[15px] text-gray-600 h-[58px]">
        <Link
          href="/"
          className="flex items-center gap-1 text-gray-500 hover:text-gray-700"
        >
          <HomeIcon />
        </Link>
        {breadcrumbPaths.map((item, index) => (
          <CategoryCrumb
            key={index}
            item={item}
            isLast={index === breadcrumbPaths.length - 1}
          />
        ))}
      </div>
      {/* Qolgan mahsulot sahifasi tarkibi */}
      <div className="bg-white border p-[23px] pb-0 shadow-sectionShadow">
        <div className="flex justify-between gap-6">
          <div>
            <ThumbsGallery mainImage={product.mainImage} />
          </div>
          <div className="flex flex-col gap-2">
            <div className="flex gap-3">
              <h2 className="font-normal text-2xl leading-9 text-textColor">
                {product.title}
              </h2>
              <Image
                width={86}
                height={31}
                className="w-[86px] h-[36px]"
                src={`${process.env.NEXT_PUBLIC_API_URL}/${product.brand?.path}`}
                alt={product.title}
              />
            </div>
            <div className="flex justify-between gap-4 flex-1">
              {/* Mahsulot haqida ma'lumotlar */}
              {/* ... */}
            </div>
          </div>
        </div>
      </div>
      <ProductDescription />
    </Container>
  );
}
